.container {
  display: flex;
  flex-wrap: wrap;
}

.title {
  display: flex;
  align-items: center;
  flex-grow: 1;
  flex-shrink: 0;
}

.link-to-api {
  flex-shrink: 0;
  padding-top: 0;
  padding-bottom: 0;
}

.info {
  padding: 0.2em 0.2em;
  margin: 0 0.2em;
  cursor: help;
}

.selector {
  margin-left: 0.5em;
  font-size: large;
}